function DontaionTopSection() {

  const bloc = useBloc(new DonationSectionBloc<DonationTopResponse>());

  React.useEffect(() => {
    bloc.dispatch(new PageStartEvent(DonationType.Top));
  });

  return (
    <BlocBuilder<DonationSectionBloc<DonationTopResponse>, DonationSectionState>
      bloc={bloc}
      builder={
        (state) => {
          if(state.isLoading)
            return (
              <CircularProgress />
            )
          const datas = state.datas as Array<DonationTopResponse>;
          return (
            <Card elevation={8} style={{padding: 8}}>
              <Typography>üèÜË¥äÂä©Ê¶ú</Typography>
              <Divider />
              <Box height={16} />
              <TableContainer>
                <Table style={{borderTopLeftRadius: 16, borderTopRightRadius: 16, overflow: "hidden"}} >
                  <TableHead style={{backgroundColor: "rgb(139,115,108)", outlineColor: "beige", outlineStyle: "outset"}}>
                    <TableRow>
                      <TableCell style={{fontWeight: "bold", color:"rgb(255,244,119)"}}>Ë¥äÂä©ËÄÖ</TableCell>
                      <TableCell style={{fontWeight: "bold", color:"rgb(255,244,119)"}} align="right">ÈáëÈ°ç</TableCell>
                    </TableRow>
                  </TableHead>
                  <TableBody style={{color: "rgb(97,63,0)"}}>
                    {
                      datas.map((data, index) => (
                        <TableRow style={{backgroundColor: "#ffffc7"}}>
                          <TableCell >
                            {
                              (state.page == 0) ?
                                (
                                  index == 0 ? `üëë${data.names[data.names.length - 1]}` :
                                  index == 1 ? `‚≠êÔ∏è${data.names[data.names.length - 1]}` :
                                  index == 2 ? `üöÄ${data.names[data.names.length - 1]}` :
                                  `${data.names[data.names.length - 1]}`
                                ) :
                                `${data.names[data.names.length - 1]}`
                            }
                          </TableCell>
                          <TableCell align="right">TWD {data.total}</TableCell>
                        </TableRow>
                      ))
                    }
                  </TableBody>
                </Table>
                <div style={{float: "right"}}>
                  <span>
                    <IconButton 
                      disabled={!state.isPrevValid}
                      onClick={(_) => bloc.dispatch(new PreviousPageEvent())}>
                      <LeftIcon />
                    </IconButton>
                    <IconButton 
                      disabled={!state.isNextValid}
                      onClick={(_) => bloc.dispatch(new NextPageEvent())}>
                      <RightIcon />
                    </IconButton>
                  </span>
                </div>
              </TableContainer>
            </Card>
          )
        }
      }
    />
  )
}


